<template>
  <div class="login">
    <form role="form" class="form-horizontal" name="RegisterForm">
      <ul class="formarea">
        <li>
          <label class="lit"></label>
          <input type="text" placeholder="" class="textbox" required v-model="name"/>
        </li>
        <li>
          <label class="lit"></label>
          <input type="password" placeholder="" class="textbox" required v-model="psw"/>
        </li>
        <li class="liLink lg_liLink">
          <span><label><input type="checkbox"/></label></span>
          <span><a href="#/register"></a></span>
          <span><a href="#/find_pwd">?</a></span>
        </li>
        <li>
          <input type="button" @click="loginAction" value="" class="button"/>
        </li>
      </ul>
    </form>
  </div>

</template>

<script>
  import router from '../router/router'

  export default{
    created () {
      this.$store.commit('changeIndexConf', {
        isFooter: false,
        isSearch: false,
        isBack: true,
        isShare: false,
        title: ''
      })
    },
    methods: {
      loginAction: function () {
        let vm = this
        let url = vm.$store.state.comm.apiUrl + 'loginapi/login';
        let params = 'username=' + this.name + '&password=' + this.psw

        this.post(url,params,function (res) {
          if(res.result === 1){
            vm.$store.commit('isLogin', res.data[0])
            alert('')
            router.push('index/fresh')
          }else{
            alert('')
          }
        })
      }
    }
  }
</script>

<style style="scss">
  @import "../../static/css/login.scss";
</style>



