<template>
  <div class="register">
    <form role="form" class="form-horizontal">
      <ul class="formarea">
        <li>
          <label class="lit"></label>
          <input type="tel" placeholder="" class="textbox" required v-model="phone"/>
        </li>
        <li>
          <label class="lit"></label>
          <input type="password" placeholder="" class="textbox" required v-model="pwd"/>
        </li>
        <li>
          <label class="lit"></label>
          <input type="password" placeholder="" class="textbox" required @blur="canEdit" v-model="pwdAgain"/>
        </li>
        <li class="liLink">
          <a href="#/login" class="fr"></a>
        </li>
        <li>
          <input type="button" disabled="disabled" ref="isSubmit" @click="register" value="" class="button"/>
        </li>
      </ul>
    </form>
  </div>
</template>

<script>
  import router from '../router/router'

  export default{
    data: function () {
      return {}
    },
    created () {
      this.$store.commit('changeIndexConf', {
        isFooter: false,
        isSearch: false,
        isBack: true,
        title: ''
      })
    },
    methods: {
      register: function () {
        let url = this.$store.state.comm.apiUrl + 'memberapi/register'
        let params = 'name=' + this.phone + '&password=' + this.pwd
        this.post(url,params,function (res) {
          if(res.result === 1){
            alert('')
            router.push('login')
          }else{
            alert('')
          }
        })
      },
      canEdit: function () {
        if (this.pwd === this.pwdAgain) {
          this.$refs.isSubmit.removeAttribute('disabled')
        } else {
          console.log('2')
        }
      }
    }
  }
</script>
<style >
  @import "../../static/css/login.scss";
</style>



